import React, { useMemo, useState } from 'react'
import { CustomTable } from '../../../reuseableComponents';
import { CustCheckbox } from '../../../commanComponent';

const RegularPayRollTable = (props) => {
    const { regularPayRollData } = props
    const payrollTableFields = [
        { key: "sn", label: "SN" },
        { key: "checkRegularPayroll", label: "select" },
        { key: "employeeName", label: "Employee Name" },
        { key: "employeeId", label: "Employee ID" },
        { key: "payDays", label: "Pay Days" },
        { key: "basic", label: "Basic" },
        { key: "hra", label: "HRA" },
        { key: "educationAllowance", label: "Education Allowance" },
        { key: "mealAllowance", label: "Meal Allowance" },
        { key: "petrolAllowance", label: "Petrol Allowance" },
        { key: "lta", label: "LTA" },
        { key: "specialAllowance", label: "Special Allowance" },
        { key: "arrear", label: "Arrear" },
        { key: "grossPay", label: "Gross Pay" },
        { key: "pfEmployeeContribution", label: "PF - Employee Contribution" },
        { key: "pfEmployerContribution", label: "PF - Employer Contribution" },
        { key: "esiEmployeeContribution", label: "ESI - Employee Contribution" },
        { key: "esiEmployerContribution", label: "ESI - Employer Contribution" },
        { key: "gmc", label: "GMC" },
        { key: "bonus", label: "Bonus" },
        { key: "tds", label: "TDS" },
        { key: "loan", label: "Loan" },
        { key: "totalEmployeeContribution", label: "Total Employee Contribution" },
        { key: "totalEmployerContribution", label: "Total Employer Contribution" },
    ];

    const loanScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        checkRegularPayroll: () => (
            <span>

                <CustCheckbox
                    key={"checkRegularPayroll"}
                    id={`checkRegularPayroll`}
                    // checked={isActive}
                    // handleChange={(e) => setIsActive(!isActive)}
                    name={"checkRegularPayroll"}
                />
            </span>
        )



    }), [regularPayRollData]);

    return (
        <>
        <CustCheckbox
            key={"checkRegularPayroll"}
            id={`checkRegularPayroll`}
            label={"Select All"}
            // checked={isActive}
            // handleChange={(e) => setIsActive(!isActive)}
            name={"checkRegularPayroll"}
            labelClass={"text-primary "}
        />
            <CustomTable
                data={regularPayRollData}
                fields={payrollTableFields}
                // isLoading={isLoading}
                pagination={true}
                // currentPage={currentPage}
                // setCurrentPage={setCurrentPage}
                // totalPages={depList?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={loanScopedSlots}
            />
        </>
    )
}

export default RegularPayRollTable
